<template>
	<li>
		<a :href="info.link" target="_blank" class="nonunderline" :class="info.name" :title="info.alt">
			<img :src="info.imgsrc" width="100%" :onerror="`this.src='${info.onerrorimg}';this.onerror=null;`"/>
			<span class="chname"><strong>{{info.alt}}</strong></span><br/>
			<span class="chname"><strong>{{info.uppername === "" ? "" : `(${info.uppername})`}}</strong></span>
		</a>
	</li>
</template>

<script>
export default {
	name: 'characbody',
	data() {
		return {
			list: Object.freeze([
				{
					name:"",
					link:"",
					imgsrc:"",
					alt:"",
					onerrorimg:'',
					uppername: "",
				},
				{
					name:"miku",
					link:"https://sekai.best/chara/21",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_21.webp",
					alt:"初音未来",
					onerrorimg:'@/../static/img/mikubody.webp',
					uppername: "Miku",
				},
				{
					name:"rin",
					link:"https://sekai.best/chara/22",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_22.webp",
					alt:"镜音铃",
					onerrorimg:'@/../static/img/rinbody.webp',
					uppername: "Rin",
				},
				{
					name:"len",
					link:"https://sekai.best/chara/23",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_23.webp",
					alt:"镜音连",
					onerrorimg:'@/../static/img/lenbody.webp',
					uppername: "Len",
				},
				{
					name:"luka",
					link:"https://sekai.best/chara/24",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_24.webp",
					alt:"巡音流歌",
					onerrorimg:'@/../static/img/lukabody.webp',
					uppername: "Luka",
				},
				{
					name:"meiko",
					link:"https://sekai.best/chara/25",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_25.webp",
					alt:"MEIKO",
					onerrorimg:'@/../static/img/meikobody.webp',
					uppername: "",
				},
				{
					name:"kaito",
					link:"https://sekai.best/chara/26",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_26.webp",
					alt:"KAITO",
					onerrorimg:'@/../static/img/kaitobody.webp',
					uppername: "",
				},
				{
					name:"tks",
					link:"https://sekai.best/chara/13",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_13.webp",
					alt:"天马司",
					onerrorimg:'@/../static/img/tksbody.webp',
					uppername: "Tks",
				},
				{
					name:"emu",
					link:"https://sekai.best/chara/14",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_14.webp",
					alt:"凤绘梦",
					onerrorimg:'@/../static/img/emubody.webp',
					uppername: "Emu",
				},
				{
					name:"nene",
					link:"https://sekai.best/chara/15",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_15.webp",
					alt:"草薙宁宁",
					onerrorimg:'@/../static/img/nenebody.webp',
					uppername: "Nene",
				},
				{
					name:"rui",
					link:"https://sekai.best/chara/16",
					imgsrc:"https://sekai-assets-1258184166.file.myqcloud.com/character/character_select_rip/chr_tl_16.webp",
					alt:"神代类",
					onerrorimg:'@/../static/img/ruibody.webp',
					uppername: "Rui",
				},
			]),
		}
	},
	props: {
		name: {
			type: String,
			required: true,
		}
	},
	computed: {
		info() {
			for (let index in this.list) {
				const info = this.list[index];
				if (this.name === info.name) {
					return info;
				}
			}
			return this.list[0];
		}
	}
}
</script>

<style scoped>
.chname {
	white-space:nowrap;
}
</style>


