<template>
  <div class="homepage">
    <div class="backgroundimg"/>
    <div class="homepagetop">
      <div class="siteiconline" width="100%" height="10%">
        <div class="siteicontextdiv">
          <span id="siteicontextspan">laoshengji</span>
        </div>
      </div>
      <div class="homepagetopnav">
        <ul>
          <li :class="{'homepagetopnavelechoose':page==='homepage'}" @click.stop="page='homepage'">
            首页
          </li>
          <li :class="{'homepagetopnavelechoose':page==='colorful_stage'}" @click.stop="page='colorful_stage'">
            彩色舞台
          </li>
          <li :class="{'homepagetopnavelechoose':page==='about'}" @click.stop="page='about'">
            关于二次元
          </li>
        </ul>
      </div>
    </div>
    
    <div class="homepagecontent">
      <div class="homepagecontentwidth">
        <homepagecontent v-show="page === 'homepage'"/>
        <colorfulstage v-show="page === 'colorful_stage'"/>
        <about v-show="page === 'about'"/>
      </div>
    </div>
  </div>
</template>


<script>
import colorfulstage from '@/components/colorful_stage/colorful_stage.vue'
import homepagecontent from '@/components/homepagecontent.vue'
import about from '@/components/about.vue'

export default {
  components: { colorfulstage,homepagecontent,about},
  name: 'homepage',
  data() {
    return {
      page: 'homepage',
    }
  }, 
}
document.addEventListener('visibilitychange', function() {
  var ishidden = document.hidden;
  if (ishidden) {
    document.title = "∑(っ°Д°;)っ  卧槽，崩溃了！！！";
  } else {
    document.title = "ヽ(^ω^)ﾉ  hh, 你终于回来了";
    setTimeout(function(){
      document.title = 'laoshengji';
    }, 2000);
  }
});
</script>

<style scoped>
.homepagetop {
  padding-left: 9%;
  padding-right: 5%;
  padding-top: 4%;
  padding-bottom: 0%;
}
.backgroundimg {
  position: fixed;
  top: 0; 
  left:0; 
  width: 100%; 
  height: 100%; 
  /* background-image: url('https://sekai-assets-1258184166.file.myqcloud.com/character/member/res014_no010_rip/card_after_training.webp'); */
  /* background-image: url('https://img.moegirl.org.cn/common/6/64/Card_233_trained.png'); */
  background-image: url('/static/img/emu_background.webp');
  background-color: rgb(204, 103, 162);
  background-size: cover;
  opacity: 0.25;
  z-index: -1;
}
/* 网站标志行 */
.siteiconline {
  display:flex;
  flex-direction: row;
  flex-wrap:nowrap;
}
/* 网站标志元素 */
.siteicontextdiv {
  color: rgb(121, 253, 127);
  font-family: 'segoe print';
  background-color:rgb(255, 183, 88) ;
  text-align: left;
  width: auto;
  height: auto;
  padding-left:1%;
  padding-right:1%;
  border-radius: 600px;
  box-shadow: rgb(0, 208, 255) 0px 0px 3px 3px;
  text-shadow: 2px 2px 2px rgb(1, 179, 39);
}
/* 网站标志文本 */
#siteicontextspan {
  font-weight:1000;
  font-size:3vw;
}

.homepagetopnav {
  margin-top: 20px;
  width:auto;
  background-color: lightpink;
  font-size: 2vw;
}
.homepagetopnav ul {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  list-style-type:none;
  margin:0;
  padding:0;
}
.homepagetopnav li {
  white-space:nowrap;
  padding-left: 2%;
  padding-right: 2%;
  padding-top: 1%;
  padding-bottom: 1%;
  width: auto;
  font-weight: 600;
}
.homepagetopnav li:hover {
  background-color:#fb7299;
  color: white;
}
.homepagetopnavelechoose {
  border-bottom: solid rgb(248, 38, 38) 4px ;
  background-color:rgb(255, 146, 173);
}

.homepagecontent {
  padding-left: 8%;
  padding-right: 8%;
  padding-top: 5%;
  padding-bottom: 10%;
  display: flex;
  align-items: center;
}
.homepagecontentwidth {
  margin:0 auto;
  max-width:800px;
  width:100%;
}
</style>


