<script>
// import bannerimgLow from '@/assets/banner-new800.png'
import virtualsingerimg from '@/assets/virtualsinger.png'
import wonderlandsshowtimeimg from '@/assets/wonderlandsshowtime.png'
import characbody from '@/components/member/characbody.vue'
import videolist from '@/components/colorful_stage/videolist.vue'
export default {
	components: { characbody, videolist },
	name: 'colorful_stage',
	data() {
		return {
			bannerinfo: Object.freeze({
					src: "https://sekai.best/images/banner-new.webp",
					onerror : "this.src='" + '@/../static/img/banner-new.webp' + "';this.onerror=null;",
				}),
			virtualsingerinfo: Object.freeze({
					src: virtualsingerimg,
					link: "https://sekai.best/unit/piapro",
					title: "团队: VIRTUAL  SINGER",
					alt: "VIRTUAL  SINGER",
					onerror : "this.src='" + "https://sekai.best/images/jp/logol_outline/logo_piapro.png" + "';this.onerror=null;",
					style: "color: black",
					mem: ['miku', 'rin', 'len', 'luka', 'meiko', 'kaito'],
				}),
			wonderlandsshowtimeinfo: Object.freeze({
					src: wonderlandsshowtimeimg,
					link: "https://sekai.best/unit/theme_park",
					title: "团队: Wonderlands × Showtime",
					alt: "Wonderlands × Showtime",
					onerror : "this.src='" + "https://sekai.best/images/jp/logol_outline/logo_theme_park.png" + "';this.onerror=null;",
					style: "color:#ff9900;",
					mem: ['tks', 'emu', 'nene', 'rui'],
				})
		}
	}
